import utils from '../base/utils';

let fonts = fonts || {
  init() {
    if (document.documentElement.className.indexOf('fonts-loaded' ) > -1) {
      return;
    }

    const fontA = new FontFaceObserver('PT Sans', {
      weight: 700
    });
    const fontB = new FontFaceObserver('PT Serif', {
      weight: 400
    });
    const fontC = new FontFaceObserver('PT Serif', {
      weight: 700
    });
    const fontD = new FontFaceObserver('PT Serif', {
      weight: 400,
      style: 'italic'
    });
    const fontE = new FontFaceObserver('PT Mono', {
      weight: 400
    });

    window.addEventListener('load', () => {
      Promise.all([
        fontA.check(), fontB.check(), fontC.check(), fontD.check(), fontE.check()
      ]).then(() => {
        document.documentElement.className += ' fonts-loaded';
        localStorage.setItem('fontsLoaded', true);
      });
    });
  }
};

fonts.init();
